import { Avatar, Box, Card, Grid, Typography } from "@material-ui/core";
import { Link as RouterLink } from "react-router-dom";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCar,
  faSackDollar,
  faUsers,
} from "@fortawesome/free-solid-svg-icons";
import { faPersonBiking } from "@fortawesome/free-solid-svg-icons";
import { PAGES } from "../../../constants";

const QuickStats2 = (props) => (
  <Box
    sx={{
      backgroundColor: "background.default",
      p: 3,
    }}
  >
    <Grid container spacing={2}>
      <Grid item xs={12} md={6} lg={3}>
        <Card
          sx={{
            alignItems: "center",
            display: "flex",
            justifyContent: "space-between",
            p: 3,
          }}
        >
          <Box
            sx={{ flexGrow: 1 }}
            // component={RouterLink}
            // to={`/${PAGES.LIST_RIDER}`}
            style={{ textDecoration: "none" }}
          >
            <Typography color="textSecondary" gutterBottom variant="overline">
              Total Revenue
            </Typography>
            <Box
              sx={{
                alignItems: "center",
                display: "flex",
                flexWrap: "wrap",
              }}
            >
              <Typography color="textPrimary" sx={{ mr: 1 }} variant="h5">
                {100}
              </Typography>
            </Box>
          </Box>
          <Avatar
            sx={{
              backgroundColor: "primary.main",
              color: "primary.contrastText",
              height: 48,
              width: 48,
            }}
          >
            <FontAwesomeIcon icon={faSackDollar} />
          </Avatar>
        </Card>
      </Grid>
      <Grid item xs={12} md={6} lg={3}>
        <Card
          sx={{
            alignItems: "center",
            display: "flex",
            justifyContent: "space-between",
            p: 3,
          }}
        >
          <Box
            sx={{ flexGrow: 1 }}
            component={RouterLink}
            to={`/${PAGES.LIST_DRIVER}`}
            style={{ textDecoration: "none" }}
          >
            <Typography color="textSecondary" gutterBottom variant="overline">
              Total Drivers
            </Typography>
            <Box
              sx={{
                alignItems: "center",
                display: "flex",
                flexWrap: "wrap",
              }}
            >
              <Typography color="textPrimary" sx={{ mr: 1 }} variant="h5">
                {props.totalDriver}
              </Typography>
            </Box>
          </Box>
          <Avatar
            sx={{
              backgroundColor: "primary.main",
              color: "primary.contrastText",
              height: 48,
              width: 48,
            }}
          >
            <FontAwesomeIcon icon={faCar} />
          </Avatar>
        </Card>
      </Grid>
      <Grid item xs={12} md={6} lg={3}>
        <Card
          sx={{
            alignItems: "center",
            display: "flex",
            justifyContent: "space-between",
            p: 3,
          }}
        >
          <Box
            sx={{ flexGrow: 1 }}
            // component={RouterLink}
            // to={`/${PAGES.LIST_RIDER}`}
            style={{ textDecoration: "none" }}
          >
            <Typography color="textSecondary" gutterBottom variant="overline">
              Total Rides
            </Typography>
            <Box
              sx={{
                alignItems: "center",
                display: "flex",
                flexWrap: "wrap",
              }}
            >
              <Typography color="textPrimary" sx={{ mr: 1 }} variant="h5">
              {props.totalRides}
              </Typography>
            </Box>
          </Box>
          <Avatar
            sx={{
              backgroundColor: "primary.main",
              color: "primary.contrastText",
              height: 48,
              width: 48,
            }}
          >
            <FontAwesomeIcon icon={faPersonBiking} />
          </Avatar>
        </Card>
      </Grid>
      <Grid item xs={12} md={6} lg={3}>
        <Card
          sx={{
            alignItems: "center",
            display: "flex",
            justifyContent: "space-between",
            p: 3,
          }}
        >
          <Box
            sx={{ flexGrow: 1 }}
            component={RouterLink}
            to={`/${PAGES.LIST_RIDER}`}
            style={{ textDecoration: "none" }}
          >
            <Typography color="textSecondary" gutterBottom variant="overline">
              Customers
            </Typography>
            <Box
              sx={{
                alignItems: "center",
                display: "flex",
                flexWrap: "wrap",
              }}
            >
              <Typography color="textPrimary" sx={{ mr: 1 }} variant="h5">
                {props.totalCustomer}
              </Typography>
            </Box>
          </Box>
          <Avatar
            sx={{
              backgroundColor: "primary.main",
              color: "primary.contrastText",
              height: 48,
              width: 48,
            }}
          >
            <FontAwesomeIcon icon={faUsers} />
          </Avatar>
        </Card>
      </Grid>
    </Grid>
  </Box>
);

export default QuickStats2;
