import WidgetPreviewer from "../../components/WidgetPreviewer";
import QuickStats2 from "../../components/widgets/quick-stats/QuickStats2";
import { Helmet } from "react-helmet-async";
import { Box, Container, Grid, Typography } from "@material-ui/core";
import { OverviewTotalTransactions } from "../../components/dashboard/overview";
import useSettings from "../../hooks/useSettings";
import { useSelector } from "react-redux";

const Overview = () => {
  const { settings } = useSettings();
  const reduxDriver = useSelector((state) => state.UData.drivers);
  const reduxUsers = useSelector((state) => state.UData.users);
  const reduxTotalRides = useSelector((state) => state.Rides.responseCompletedRides);
  return (
    <>
      <Helmet>
        <title>Overview | Hopon Admin</title>
      </Helmet>
      <Box
        sx={{
          backgroundColor: "background.default",
          minHeight: "100%",
          py: 8,
        }}
      >
        <Container maxWidth={settings.compact ? "xl" : false}>
          <Grid container spacing={3}>
            <Grid
              alignItems="center"
              container
              justifyContent="space-between"
              spacing={3}
              item
              xs={12}
            >
              <Grid item>
                <Typography color="textPrimary" variant="h5">
                  Dashboard
                </Typography>
              </Grid>
            </Grid>
            <Grid item md={11} xs={12}>
              <WidgetPreviewer
                element={
                  <QuickStats2
                    totalDriver={Object.values(reduxDriver).length}
                    totalCustomer={Object.values(reduxUsers).length}
                    totalRides={Object.values(reduxTotalRides).length}
                  />
                }
              />
            </Grid>
            <Grid item md={11} xs={12}>
              <OverviewTotalTransactions />
            </Grid>
          </Grid>
        </Container>
      </Box>
    </>
  );
};

export default Overview;
