import Chart from "react-apexcharts";
import { format } from "date-fns";
import { Box, Card, CardHeader, Typography } from "@material-ui/core";
import { useTheme } from "@material-ui/core/styles";
import Scrollbar from "../../Scrollbar";

let months = [
  "January",
  "February",
  "March",
  "April",
  "May",
  "June",
  "July",
  "August",
  "September",
  "October",
  "November",
  "December",
];
let currentMonth = new Date().getMonth();
var profitChart = {
  labels: months
    .slice(currentMonth - 11)
    .concat(months.slice(0, currentMonth + 1)),
  datasets: [
    {
      label: "Profit",
    },
  ],
};
const data = {
  series: [
    {
      data: [2, 4, 6, 8, 8, 10, 12, 13, 14, 15, 16, 20],
    },
  ],
  categories: profitChart.labels,
};

const OverviewTotalTransactions = (props) => {
  const theme = useTheme();

  const chartOptions = {
    chart: {
      background: "transparent",
      stacked: true,
      toolbar: {
        show: false,
      },
    },
    colors: ["#2DD36F"],
    dataLabels: {
      enabled: false,
    },
    grid: {
      borderColor: theme.palette.divider,
      xaxis: {
        lines: {
          show: true,
        },
      },
      yaxis: {
        lines: {
          show: true,
        },
      },
    },
    states: {
      active: {
        filter: {
          type: "darken",
          value: 0.35,
        },
      },
      hover: {
        filter: {
          type: 'lighten',
          value: 0.15,
        },
      },
    },
    legend: {
      show: false,
    },
    stroke: {
      colors: ["transparent"],
      show: true,
      width: 2,
    },
    theme: {
      mode: theme.palette.mode,
    },
    xaxis: {
      axisBorder: {
        show: false,
      },
      axisTicks: {
        show: false,
      },
      categories: data.categories,
      labels: {
        style: {
          colors: theme.palette.text.secondary,
        },
      },
    },
    yaxis: {
      labels: {
        offsetX: -12,
        style: {
          colors: theme.palette.text.secondary,
        },
      },
    },
  };

  const chartSeries = data.series;

  return (
    <Card {...props}>
      <CardHeader
        subheader={
          <Typography color="textSecondary" variant="body2">
            {format(new Date(), "MMM yyyy")}
          </Typography>
        }
        title="LAST 12 MONTH REVENUE"
      />
      <Scrollbar>
        <Box
          sx={{
            height: 380,
            minWidth: 600,
            px: 2,
          }}
        >
          <Chart
            height="350"
            options={chartOptions}
            series={chartSeries}
            type="bar"
          />
        </Box>
      </Scrollbar>
    </Card>
  );
};

export default OverviewTotalTransactions;
